import { useState, useEffect } from "react";
import {Routes, Route, Link, Outlet} from 'react-router-dom';
import './blog.css';
import BlogPost from "./BlogPost";

const query = 
`{
    pageBlogPostCollection {
      items {
        title
        slug
        featuredImage{
          url
        }
        content {
            json
        }
      }
    }
}`;


function Blog() {
  const [blogPosts, setBlogPosts] = useState(null);

  useEffect(() => {
    window
      .fetch(`https://graphql.contentful.com/content/v1/spaces/${process.env.REACT_APP_CONTENTFUL_SPACE_ID}/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${process.env.REACT_APP_CONTENTFUL_DELIVERY_TOKEN}`,
        },
        body: JSON.stringify({ query }),
      })
      .then((response) => response.json())
      .then(({ data, errors }) => {
        if (errors) {
          console.error(errors);
        }

        setBlogPosts(data.pageBlogPostCollection.items);
      });
  }, []);
  if (!blogPosts) {
    return <div className="blog">"Loading..."</div>;
  }

  function BlogPosts(blogPosts) {
    return (
        <div className="blog">
            {blogPosts.blogPosts.map((post) => (
            <div key={`${post.slug}`} className="blog-post-card">
                <h3><Link to={`${post.slug}`}>{post.title}</Link></h3>
                <div>
                    <img src={`${post.featuredImage?.url}`}
                      alt={`${post.slug}`} />
                </div>
                <div className="content">
                    <Outlet />
                </div>
            </div>
            ))}
        </div>
    );
    }
  return (
      <div className="blog-posts">
          <Routes>
            <Route index element={<BlogPosts blogPosts={blogPosts} />} />
            {blogPosts.map((post) => {
                return (<Route exact path=":postId" element={<BlogPost posts={blogPosts}/>}/>);
            })}
          </Routes>
      </div>
  )
}

export default Blog;